.navbar {
  z-index: 10;
  background-color: rgb(233, 239, 255);
  border-width: 1px;
  border-color: rgb(189, 209, 255);
  text-align: right;
  /* display: inline; */
  position: sticky;
  top: 0;
  border: 2px outset rgb(189, 209, 255);
  border-top: 0px;
}

#navbarList {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  padding-top: 10px;
  padding-right: 10px;
  justify-content: right;
  margin-top: -20px;
  /* display: inline; */
}